import React from "react";
import "normalize.css/normalize.css";
import { createGlobalStyle } from "styled-components";
import { ThemeProvider, Styled } from "theme-ui";
import Clifton from "./fonts/Clifton-Italic.woff2";
import Maison from "./fonts/MaisonNeue-Book.woff2";
import Authentic from "./fonts/authentic.woff2";

import theme, { rem } from "@src/theme";
export const GlobalStyle = createGlobalStyle`



@font-face {
  font-family: "Clifton";
src: url(${Clifton}) format("woff2");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Maison";
src: url(${Maison}) format("woff2");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Authentic";
src: url(${Authentic}) format("woff2");
  font-style: normal;
  font-weight: 700;
}


  * {
    box-sizing: border-box;

  }

  button {
    cursor: pointer;
  }

  html {
    -webkit-print-color-adjust: exact;
    font-family: ${theme.fonts.body};
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.2;
    position: relative;
    color: ${theme.colors.black};
    overflow-x: hidden;
    background: #f9f9f9;
  }


  body {
    min-height: 100%;
  }



  ul {
    margin: 0;
    padding: 0;
  }



  button {
    background: none;
    border: none;
    padding: 0;
    margin: 0;
  }

  /* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance:textfield;
}



ul {
  margin: 0;
}
button, div[role="button"] {
  outline: none;
}
`;

export default ({ element }) => (
  <React.Fragment>
    <ThemeProvider theme={theme}>
      <GlobalStyle />

      <Styled.root>{element}</Styled.root>
    </ThemeProvider>
  </React.Fragment>
);
